.table_data {
  border: 4px solid var(--default-color);
  width: 50%;
  padding: 20px 3%;
}

@media only screen and (max-width: 425px) {
  .table_data {
    padding: 10px;
    padding-left: 5%;
  }
}

.table {
  border-collapse: collapse;
  border: 2px solid var(--default-color);
  padding: 2px;
}
